.footer {
    padding-bottom: 100px;
    padding-top: 100px;
  
    background-color: var(--primary-color);
    color: var(--white-color);
  }
  
  .footer__flex {
    display: grid;
    grid-template-columns: 280px 280px 576px;
    grid-gap: 16px;
  }
  
  .footer__box {
    line-height: 1.43;
    letter-spacing: 2px;
    text-transform: uppercase;
  }
  
  .footer__pages-list {
    margin-top: 11px;
  }
  
  .footer__page-item:not(:last-child) {
    margin-bottom: 12px;
  }
  
  .footer__page-link {
    line-height: 1.43;
    color: var(--white-color);
  
    transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  
  }
  
  .footer__page-link:hover,
  .footer__page-link:focus{
    color: var(--accent-color);
  
  }
  
  .footer__social-list {
    display: flex;
    align-items: center;
    justify-content: start;
    margin-top: 20px;
  }
  
  .footer__social-item:not(:last-child) {
    margin-right: 20px;
  }
  
  .footer-icon{
    fill: var(--white-color);
  
    transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1);
  }
  
  .footer-icon:hover{
    fill: var(--accent-color);
  }
  
  .footer__submit{
    position: relative;
  }
  
  .footer__submit-text {
    width: 563px;
    line-height: 1.43;
    margin-bottom: 10px;
  }
  
  .footer__submit-input {
    width: 576px;
    margin: 0;
    padding: 16px 30px;
    border-radius: 8px;
  
    border: 1px solid #e5e7eb;
    background-color: #f9fafb;
    color: var(--primary-color);
  
    transition: border-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  }
  
  .footer__submit-input::placeholder {
    font-family: 'Montserrat';
    font-style: normal;
    line-height: 1.43;
    font-weight: 400;
    font-size: 14px;
  
    color: var(--primary-color);
  }
  
  .footer__submit-input:hover,
  .footer__submit-input:focus{
    outline: none;
  
    border-color: var(--accent-color);
  }
  
  .footer__submit-btn {
    position: absolute;
    right: 11px;
    min-height: 30px;
    margin-top: 9px;
  
    border: none;
    padding: 5px;
    border-radius: 10px;
  
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 1.43;
  
    background-color: var(--primary-color);
    color: var(--white-color);
  
    cursor: pointer;
  
    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  }
  
  .footer__submit-btn:hover,
  .footer__submit-btn:focus {
    background-color: var(--accent-color);
  }
  
  .footer__logo {
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: 48px;
    width: 200px;
  }
  
  .footer__logo{
    width: 200px;
  }
  
  .footer__text {
    margin-top: 20px;
  
    line-height: 1.43;
    text-align: center;
  }
  
  .footer__text-link {
    line-height: 1.43;
  
    color: var(--white-color);
  
    transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  }
  
  .footer__text-link:hover,
  .footer__text-link:focus {
    color: var(--accent-color);
  }
  
  .footer__contacts-list{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
  }
  
  .footer__contacts-item:not(:first-child)::before{
    position: absolute;
    top: 10px;
    left: -18px;
  
    content: "";
    width: 12px;
    height: 1px;
    border: 0px solid #fff;
    background-color: var(--white-color);
  }
  
  .footer__contacts-item{
    position: relative;
  }
  
  .footer__contacts-item:not(:last-child){
    margin-right: 22px;
  }
  
  .footer__contact-link{
    line-height: 1.43;
  
    color: var(--white-color);
  }
  
  .footer__contact-link:hover,
  .footer__contact-link:focus{
    color: var(--accent-color);
  }